.contact-form {
  max-width: 360px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 16px;
}

.contact-form > :not(:last-child) {
  margin-bottom: 16px;
}

.contact-form .form-field {
  display: flex;
  flex-direction: column;
}

.contact-form .label-content {
  margin-bottom: 4px;
}

.contact-form input,
.contact-form textarea {
  display: flex;
  flex: 1 1;
  width: 100%;
  max-width: 100%;
}

.contact-form label {
  text-align: left;
}

.page {
  width: 100vw;
  height: 100vh;
}

.slider {
  position: relative;
  width: 100%;
  height: 25vh;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  overflow: hidden;
  background: black;
}

.slide {
  position: relative;
  min-width: 100%;
  height: 24vh;
  transition: 0.5s;
  overflow: hidden;
}

#goLeft {
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translateY(-50%);
  width: 5%;
  height: 80%;
  background: none;
  border: none;
  outline: none;
  transition: 0.5s;
}

#goLeft:hover {
  background: rgba(0, 0, 0, 0.349);
  cursor: pointer;
}

#goRight {
  position: absolute;
  top: 50%;
  right: 0%;
  transform: translateY(-50%);
  width: 5%;
  height: 80%;
  background: none;
  border: none;
  outline: none;
  transition: 0.5s;
}
#goRight:hover {
  background: rgba(0, 0, 0, 0.349);
  cursor: pointer;
}

#goRight:hover i {
  font-size: 2vw;
  color: whitesmoke;
  transition: 0.3s;
}

#goLeft:hover i {
  font-size: 2vw;
  color: whitesmoke;
  transition: 0.3s;
}

