.mainTitle {
  margin: 25px;
}
.container-fluid {
  width: 66vw;
  margin-left: 17vw;
  padding: 0px;
}
.modalClose {
  float: right;
}

.row {
  margin: 0;
  padding: 0;
}

.slide {
  height: 35vh;
}

.modal-header {
  height: 36vh;
}
@-webkit-keyframes slideUp {
  from {
    margin-top: 20%;
  }
  to {
    margin-bottom: 25%;
  }
}
@keyframes slideUp {
  from {
    margin-top: 20%;
  }
  to {
    margin-bottom: 25%;
  }
}

@-webkit-keyframes slideDown {
  from {
    margin-bottom: 50%;
    height: 70%;
  }
  to {
    margin-top: 24%;
    height: 100%;
  }
}

@keyframes slideDown {
  from {
    margin-bottom: 50%;
    height: 70%;
  }
  to {
    margin-top: 24%;
    height: 100%;
  }
}

.cardBtn {
  background-color: white !important;
  color: black !important;
  outline-style: solid;
  outline-color: #5085a5;
}
.title {
  font-size: x-large;
  text-align: center;
}

.textArea {
  font-size: large;
  margin-top: 10px;
  text-align: center;
  color: #5085a5;
}
.displayImg {
  width: 100%;
  height: 100%;
}

.cardInfo {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}

.Holder {
  position: relative;
  width: 100%;
  height: 100%;
}
.Holder .cardBtn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: #555;
  color: white;
  font-size: 16px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  display: none;
}

.Holder .cardInfo {
  display: none;
}

.Holder.cardBtn:hover {
  display: block;
}
.Hovered {
  position: relative;
  width: 100%;
  height: 100%;
}
.Hovered .cardBtn {
  position: absolute;
  top: 75%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: #555;
  color: white;
  font-size: 16px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  -webkit-animation-name: slideUp;
          animation-name: slideUp;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.Hovered .cardInfo {
  -webkit-animation-name: slideDown;
          animation-name: slideDown;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.Hovered .displayImg {
  opacity: 0;
  transition-duration: 0.25s;
}

